import accountApi from "apis/authApi";
import React, { useEffect, useState } from "react";
import { Form, Button } from "react-bootstrap";
import { useHistory, useLocation } from "react-router";
import { Link } from "react-router-dom";

function RenewPassword() {
  document.title = "Ly li mt khu";

  const [password, setPassword] = useState("");
  const [passwordConfirm, setPasswordConfirm] = useState("");
  const [err, setErr] = useState("");

  const history = useHistory();
  const location = useLocation();

  const tokenActive = new URLSearchParams(location.search).get("token");
  //   console.log("c chy r", tokenActive);
  //   useEffect(() => {
  //     activePassword();
  //   }, []);

  const resetPassword = async (password) => {
    const params = { password: password };
    try {
      await accountApi.postResetPasword(tokenActive, params);
      setErr("Ly li mt khu thnh cng");
    } catch (error) {
      console.log("li ri", { error });
    }
  };

  const handleResetPassword = (e) => {
    e.preventDefault();
    if (!password || !passwordConfirm) {
      setErr("Mt khu khng c b trng");
    } else if (password !== passwordConfirm) {
      setErr("Mt khu xc nhn khng chnh xc");
    } else {
      resetPassword(password);
    }
  };

  return (
    <div className="formMain">
      <div className="formMain_container">
        <Form className="formMenu" onSubmit={handleResetPassword}>
          <legend className="form_name">Thay i Mt Khu</legend>
          <Form.Group className="mb-3 formMenu_items">
            <Form.Label className="formMenu_items_name">
              Nhp mt khu mi
            </Form.Label>
            <Form.Control
              className="formMenu_items_value"
              name="title"
              type="password"
              placeholder="Nhp mt khu mi"
              value={password}
              onChange={(e) => setPassword(e.target.value)}
            />
          </Form.Group>
          <Form.Group className="mb-3 formMenu_items">
            <Form.Label className="formMenu_items_name">
              Nhp li mt khu mi
            </Form.Label>
            <Form.Control
              className="formMenu_items_value"
              name="title"
              type="password"
              placeholder="Xc nhn ng mt khu mi"
              value={passwordConfirm}
              onChange={(e) => setPasswordConfirm(e.target.value)}
            />
          </Form.Group>
          <div className=" mb-3 formMenu_items_link">
            <p>
              {err && (
                <span>
                  {err} <Link to="/login">Quay Li ng Nhp?</Link>
                </span>
              )}
            </p>
          </div>
          <Button type="submit" variant="success" style={{ width: "100%" }}>
            Thay i mt khu
          </Button>
        </Form>
      </div>
    </div>
  );
}

export default RenewPassword;
